import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import logo from '../assets/images/logo.png';
class HeaderComponent extends Component {
    render() {
        return (
            <nav className="navbar navbar-default navbar-fixed-top" style={{background:'#fff'}}>
                <div className="container">
                    <div className="navbar-header">
                        <Link className="navbar-brand" to="#header">
                            <img src={logo} alt="" />
                        </Link>
                        <Link className="icon-label" to="#header">
                            <i className="ti-home"></i>
                            <div>Process</div>
                        </Link>
                        <Link className="icon-label" to="/page-2">
                            <i className="ti-pencil-alt"></i>
                            <div>Features</div>
                        </Link>
                        <Link className="icon-label" to="/blog">
                            <i className="ti-pencil"></i>
                            <div>Blog</div>
                        </Link>
                        <Link className="icon-label" to="#contact">
                            <i className="ti-mobile"></i>
                            <div>Contact</div>
                        </Link>
                    </div>
                    <div className="collapse navbar-collapse" id="main-navbar">
                        <ul className="nav navbar-nav navbar-right">
                            <li>
                                <Link to="#process">Process</Link>
                            </li>
                            <li>
                                <Link to="#features">Feature</Link>
                            </li>
                            <li>
                                <Link to="/blog">Blog</Link>
                            </li>
                            <li>
                                <Link to="#contact">Contact Us</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }
}

export default HeaderComponent;


